<template>
  <transition name="slide">
    <div class="user-center">
      <div class="back" @click="back()">
        <i class="icon-back"></i>
      </div>
      <div class="switches-wrapper">
        <switches :switches="switches" :currentIndex="currentPage" @switchToggle="toggle"></switches>
      </div>
      <div ref="playBtn" class="play-btn" @click="randomAll">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
      <div class="list-wrapper" ref="listWrapper">
        <scroll ref="favoriteList" class="list-scroll" v-if="currentPage===0" :data="favoriteList">
          <div class="list-inner">
            <song-list :songs="favoriteList" @select="playSong"></song-list>
          </div>
        </scroll>
        <scroll ref="playList" class="list-scroll" v-if="currentPage===1" :data="playHistory">
          <div class="list-inner" @click.stop>
            <song-list :songs="playHistory" @select="playSong"></song-list>
          </div>
        </scroll>
      </div>
    </div>
  </transition>
</template>

<script>
  import Switches from '../../base/switches/switches.vue'
  import SongList from '../../base/song-list/song-list'
  import Scroll from '../../base/scroll/scroll'
  import TopTip from '../../base/top-tip/top-tip'
  import {mapGetters, mapActions,mapMutations} from 'vuex'
  import Song from '../../common/js/song'
  import {playListMixin} from '../../common/js/mixin'

  export default {
    mixins: [playListMixin],
    data() {
      return {
        currentPage: 0,
        switches: [
          {name: '我喜欢的'},
          {name: '最近听的'}
        ],
      }
    },
    computed:{
      ...mapGetters([
        'playHistory',
        'favoriteList',
        'currentIndexPlay',
        'currentSong'
      ]),
    },
    mounted(){
      this.songs=this.favoriteList;
    },
    methods: {
      //切换显示列表
      toggle(index){
        if(!index){
          this.currentPage=0;
          this.songs=this.favoriteList
        }
        else {
          this.currentPage=1;
          this.songs=this.playHistory
        }
      },
      //返回
      back() {
        this.$router.back()
      },
      //选择歌曲播放
      playSong(song){
        if(song.id===this.currentSong.id){
          return;
        }
        this.insertSong(song);//插入
        this.setCurrentPlay(this.currentIndexPlay+1);//立即播放
        if(!this.playing){
          this.setPlayingState(true);
        }
      },
      //随机播放全部
      randomAll(){
        this.randomPlay(this.songs);
      },
      handlePlayList(playList) {
        const bottom = playList.length > 0 ? '60px' : '';
        this.$refs.listWrapper.style.bottom = bottom;
        if(this.currentPage===0)
        this.$refs.favoriteList.refresh();
        if(this.currentPage===1)
        this.$refs.playList.refresh()
      },
      ...mapMutations({
        setCurrentPlay:'SET_CURRENT_PLAY',
        setPlayingState:'SET_PLAYING'
      }),
      ...mapActions([
        'insertSong',
        'randomPlay'
      ])
    },
    components: {
      Switches,
      Scroll,
      SongList,
      TopTip
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .user-center
    position: fixed
    top: 0
    bottom: 0
    z-index: 100
    width: 100%
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .switches-wrapper
      padding: 20px 0
      background-color $color-user-bg
    .play-btn
      box-sizing: border-box
      width: 135px
      padding: 7px 0
      margin: 20px auto
      text-align: center
      border: 1px solid  $color-text-l
      color: $color-text-l
      border-radius: 100px
      font-size: 0
      .icon-play
        display: inline-block
        vertical-align: middle
        margin-right: 6px
        font-size: $font-size-medium-x
      .text
        display: inline-block
        vertical-align: middle
        font-size: $font-size-small
    .list-wrapper
      position: absolute
      top: 125px
      bottom: 0
      width: 100%
      .list-scroll
        height: 100%
        overflow: hidden
        .list-inner
          padding: 20px 30px
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>




